* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* VARIABLES */

/* GLOBAL STYLES */
html {
  font-size: 10px;
}

body,
html {
  height: 100%;
  min-height: 100vh;
}

body {
  font-size: 1.6rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  font-family: "Winky Rough", sans-serif;
}

.container {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
}

header {
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 32px;
}

header .logo img {
  width: 96px;
}

.search-bar form {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.search-bar input {
  padding: 8px 12px;
  font-size: 1.8rem;
  outline: none;
  border-radius: 8px 0 0 8px;
  border: none;
  width: 450px;
  font-family: "Winky Rough", sans-serif;
}

.search-bar button {
  padding: 8px 12px;
  border-radius: 0 8px 8px 0;
  font-size: 1.8rem;
  box-sizing: border-box;
  outline: none;
  border: none;
  color: #fff;
  background-color: #000;
  font-family: "Winky Rough", sans-serif;

  &:hover {
    background-color: #323232;
  }

  &:active {
    background-color: #666;
  }
}

header .temperature-converter button {
  outline: none;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  background-color: #000;
  color: #fff;
  font-size: 1.8rem;
  font-family: "Winky Rough", sans-serif;

  &:hover {
    background-color: #323232;
  }

  &:active {
    background-color: #666;
  }
}

main {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

main .weather-card {
  width: 600px;
  color: white;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: none;

  .date-time {
    font-size: 8rem;
    font-weight: 700;
  }

  .conditions {
    display: flex;
    align-items: center;
    gap: 32px;

    .temp-and-condition {
      font-size: 4.8rem;
      font-weight: 500;

      .temperature {
        text-align: center;
      }

      .condition {
        text-align: center;
      }
    }

    .icon img {
      width: 150px;
    }
  }

  .location {
    font-size: 4.8rem;
    font-weight: 500;
    text-align: center;
  }

  .description {
    font-size: 3.2rem;
    font-weight: 500;
    text-align: center;
  }
}

main .loading {
  width: 600px;
  color: white;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto 0;
  gap: 8px;
  display: none;

  .spinner {
    width: 120px;
    height: 120px;
    border: 12px solid white;
    border-bottom: 12px solid black;
    border-radius: 50%;
    animation: rotate 1s linear infinite;
    margin-bottom: 8px;
  }

  .text {
    font-size: 4.8rem;
    text-align: center;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

main .error {
  width: 600px;
  color: white;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto 0;
  gap: 8px;
  display: none;
  font-size: 6.4rem;
}

footer {
  text-align: center;
  margin: 8px;
  color: #fff;

  a {
    text-decoration: underline;
    color: #ccc;

    &:hover {
      background-color: #000;
    }
  }
}
